<template>
  <header id="header" class="alt">
    <h1><a :href="domain">GitHub Proxy</a></h1>
    <nav id="nav">
      <ul>
        <li v-for="item in navItems" :key="item.text">
          <a :href="item.link" :target="item.target">{{ item.text }}</a>
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
import { computed, ref } from 'vue';

export default {
  name: 'AppHeader',
  setup() {
    const domain = computed(() => `https://${window.location.hostname}`);
    const navItems = ref([
      { text: '首页', link: domain.value },
      { text: 'Docker镜像代理', link: 'https://docker.imixc.top/', target: '_blank' },
      { text: '博客', link: 'https://ixingchen.org.cn/', target: '_blank' }
    ]);

    return { domain, navItems };
  }
};
</script>

<style scoped>
/* 添加适当的样式 */
</style>