<template>
  <footer id="footer">
    <ul class="copyright">
      <li>©{{ capitalizedDomain }}. All Rights Reserved.</li>
      <li>Github Project : <a href="https://github.com/hunshcn/gh-proxy" target="_blank">hunshcn/gh-proxy</a></li>
      <li>UI : <a href="https://mirror.ghproxy.com/" target="_blank">ghproxy.com</a></li>
    </ul>
    <center>
    </center>
  </footer>
</template>

<script>
export default {
  name: 'PageFooter',
  data() {
    return {
      domain: window.location.hostname
    };
  },
  computed: {
    capitalizedDomain() {
      return this.domain.charAt(0).toUpperCase() + this.domain.slice(1);
    }
  },
  mounted() {
    this.loadScripts([
      '../../assets/js/breakpoints.min.js',
      '../../assets/js/browser.min.js',
      '../../assets/js/jquery.dropotron.min.js',
      '../../assets/js/jquery.scrollex.min.js',
      '../../assets/js/jquery.min.js',
      '../../assets/js/main.js',
      '../../assets/js/util.js'
    ]);
  },
  methods: {
    loadScripts(scripts) {
      scripts.forEach(src => {
        const script = document.createElement('script');
        script.src = src;
        script.async = true;
        document.body.appendChild(script);
      });
    }
  }
};
</script>

