<template>
  <div>
    <header>
      <h2>使用说明</h2>
      <p align="left" v-html="importantNotice"></p>
    </header>
    <div class="row">
      <div class="col-12">
        <section class="box" v-for="(section, index) in instructions" :key="index">
          <h3>{{ section.title }}</h3>
          <div v-html="section.content"></div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, ref } from 'vue';

export default {
  name: 'UserInstructions',
  setup() {
    const domain = computed(() => `https://${window.location.hostname}`);
    const noHttpsDomain = computed(() => window.location.hostname);

    const importantNotice = '<font color="red">重要事件: </font>本站为个人维护的爱发电项目,其目的是为广大开发者提供 GitHub 代理下载服务. <br> 并且不鼓励一些滥用的行为. <br> <br> 如果本站的服务帮助到您,欢迎大家随意<a href="" target="_blank">打赏</a>或<font color="red">提供服务器赞助</font>以支持本站提供线路更好的服务.';

    const instructions = ref([
      {
        title: '终端命令行',
        content: `
          <p>支持终端命令行 git clone , wget , curl 等工具下载. <br> 支持 <font color="red">raw.githubusercontent.com</font> , <font color="red">gist.github.com </font> , <font color="red">gist.githubusercontent.com</font> 文件下载. <br> <b>注意：</b>不支持 SSH Key 方式 git clone 下载. </p>
          <hr>
          <h4><b>git clone</b></h4>
          <p><b>git clone</b> <font color="red">${domain.value}/</font><font color="green">https://github.com/stilleshan/ServerStatus</font> </p>
          <h4><b>git clone私有仓库</b></h4>
          <p> Clone 私有仓库需要用户在 <a href="https://github.com/settings/tokens" target="_blank">Personal access tokens</a> 申请 Token 配合使用. <br><b>git clone</b> <font color="red">https://</font><b>user:your_token</b><font color="red">@${noHttpsDomain.value}/</font><font color="green">https://github.com/your_name/your_private_repo</font></p>
          <h4><b>wget &amp; curl</b></h4>
          <p><b>wget</b> <font color="red">${domain.value}/</font><font color="green">https://github.com/stilleshan/ServerStatus/archive/master.zip</font> <br> <b>wget</b> <font color="red">${domain.value}/</font><font color="green">https://raw.githubusercontent.com/stilleshan/ServerStatus/master/Dockerfile</font> <br> <b>curl -O</b> <font color="red">${domain.value}/</font><font color="green">https://github.com/stilleshan/ServerStatus/archive/master.zip</font> <br> <b>curl -O</b> <font color="red">${domain.value}/</font><font color="green">https://raw.githubusercontent.com/stilleshan/ServerStatus/master/Dockerfile</font> </p>
        `
      },
      {
        title: '首页下载',
        content: `
          <p>在本页地址栏输入合规链接（参考以下链接）点击下载按钮 <br> 支持 <font color="red">raw.githubusercontent.com</font> , <font color="red">gist.github.com </font> , <font color="red">gist.githubusercontent.com</font> 文件下载. </p>
          <hr>
          <h4><b>Raw 文件</b></h4>
          <p>https://raw.githubusercontent.com/stilleshan/ServerStatus/master/Dockerfile</p>
          <h4><b>分支源码</b></h4>
          <p>https://github.com/stilleshan/ServerStatus/archive/master.zip</p>
          <h4><b>Releases 源码</b></h4>
          <p>https://github.com/stilleshan/ServerStatus/archive/v1.0.tar.gz</p>
          <h4><b>Releases 文件</b></h4>
          <p>https://github.com/fatedier/frp/releases/download/v0.33.0/frp_0.33.0_linux_amd64.tar.gz</p>
        `
      }
    ]);

    return { importantNotice, instructions };
  }
};
</script>