<template>
  <main>
    <section id="cta">
      <!-- 修改图片路径 -->
      <img src="/assets/images/github.png" alt="GitHub Logo" height="100" width="100">
      <h2>GitHub Proxy Server</h2>
      <p>GitHub 文件, Releases, archive, gist, raw.githubusercontent.com 文件代理加速下载服务.</p>
      <form @submit.prevent="handleSubmit">
        <div class="row gtr-50 gtr-uniform">
          <div class="col-8 col-12-mobilep">
            <input class="block url" type="text" v-model="gh_url" placeholder="输入 GitHub 文件链接" :pattern="urlPattern" required>
          </div>
          <div class="col-4 col-12-mobilep">
            <input type="submit" value="下载">
          </div>
        </div>
      </form>
    </section>
    <section id="main" class="container">
      <Instructions />
    </section>
  </main>
</template>

<script>
import { ref } from 'vue';
import Instructions from './Instructions.vue';

export default {
  name: 'MainContent',
  components: { Instructions },
  setup() {
    const gh_url = ref('');
    const urlPattern = '^((https|http):\\/\\/)?(github\\.com\\/.+?\\/.+?\\/(?:releases|archive|blob)|((?:raw|gist)\\.(?:githubusercontent|github)\\.com))\\/.+$';

    const handleSubmit = () => {
      if (gh_url.value) {
        window.open(`https://github.axingchen.com/${gh_url.value}`, '_blank');
      }
    };

    return { gh_url, urlPattern, handleSubmit };
  }
};
</script>

